<template>
  <main>
    <div class="page-grid">
      <header class="page-head">
        <h1 class="page-title">Авторизация</h1>
      </header>
      <form class="input-wr" ref="form">
        <div class="input">
          <span class="input__title">Логин</span>
          <label>
            <input
                ref="login"
                type="text"
                autocomplete="username"
                placeholder="mail@mail.ru"
                required
                name="login">
          </label>
        </div>
        <div class="input">
          <span class="input__title">Пароль</span>
          <label>
            <input
                ref="password"
                type="password"
                placeholder="***"
                required
                name="password">
          </label>
        </div>
        <button class="add" @click.prevent="login">
          Войти
        </button>
      </form>
    </div>
  </main>
</template>

<script>
import {setCookie} from "@/assets/cookie";

export default {
  name: "Login",

  methods: {
    login() {
      this.$refs.login.value === 'rukovoditel' && this.$refs.password.value === '123'
          ? (this.$store.commit('setAdmin', true), setCookie('isAdmin', true, {'max-age': 93600}), this.$router.push('/'))
          : (this.$refs.login.classList.add('error'), this.$refs.password.classList.add('error'))

      this.$refs.login.value === 'aspirant' && this.$refs.password.value === '123'
          ? (this.$store.commit('setAdmin', false), setCookie('isAdmin', false, {'max-age': 93600}), this.$router.push('/'))
          : (this.$refs.login.classList.add('error'), this.$refs.password.classList.add('error'))
    }
  }
}
</script>

